import React, { FC } from 'react';
import { Menu } from 'antd';
import { NavLink } from 'react-router-dom';
import { RouteNames } from '../../../router';

const Navigation: FC = () => {
  return (
    <div>
      <Menu theme="light" mode="horizontal">
        <Menu.Item key={0}>
          <NavLink to={RouteNames.PARKING}></NavLink>
        </Menu.Item>
        <Menu.Item key={1}>
          <NavLink to={RouteNames.INSURANCE}></NavLink>
        </Menu.Item>
        <Menu.Item key={2}>
          <NavLink to={RouteNames.PETROL}></NavLink>
        </Menu.Item>
        <Menu.Item key={3}>
          <NavLink to={RouteNames.SERVICE}></NavLink>
        </Menu.Item>
      </Menu>
    </div>
  );
};

export default Navigation;
